import React from 'react';
import styled from 'styled-components';

const ProductItemBlock = styled.div`
    width:50%;
    display:flex;
    margin-bottom:30px; align-items:center;
    img { width:130px; }
    div {
        padding:0 10px;
        p:nth-child(1) {font-size:20px;}
        p:nth-child(2) {font-size:18px; margin:10px 0; }
    }
`

const Productitem = ({name, depart, about, photo}) => {
    return (
        <ProductItemBlock>
            <img src={photo} alt={about} />
            <div>
                <p>{name}</p>
                <p>{depart}</p>
                <p>{about}</p>
            </div>
        </ProductItemBlock>
    );
};

export default Productitem;